import React, { useState, useEffect } from 'react'
import { faHome, faTags, faTag } from '@fortawesome/free-solid-svg-icons'

import { AppContext } from '@src-components/contexts'
import { SEO, Layout } from '@src-components/foundations'
import {
	PageHeading,
	TaxonomyList,
	TaxonomyListItem,
} from '@src-components/parts'

import { AllTagContentQuery, AllTagsGroupQuery } from '@src-queries'

const TagsListTemplate = props => {
	const { location } = props

	const allTagContentData = AllTagContentQuery()
	const allTagsGroupData = AllTagsGroupQuery()

	const [isOpenMenu, setIsOpenMenu] = useState(false)
	const [isOpenSearch, setIsOpenSearch] = useState(false)
	const [isDarkMode, setIsDarkMode] = useState(null)

	const pageTitle = 'タグ一覧'
	const pageDescription = '「言乃葉 テナ」サイト内記事のタグ一覧です。'

	const pageValues = {
		title: pageTitle,
		description: pageDescription,
		location: location.pathname,
		breadcrumbItems: [
			{ label: 'ホーム', url: '/', icon: faHome },
			{ label: pageTitle, url: location.pathname, icon: faTags },
		],
		isOpenMenu: isOpenMenu,
		setIsOpenMenu: setIsOpenMenu,
		isOpenSearch: isOpenSearch,
		setIsOpenSearch: setIsOpenSearch,
		isDarkMode: isDarkMode,
		setIsDarkMode: setIsDarkMode,
	}

	return (
		<AppContext.Provider value={pageValues}>
			<SEO />
			<Layout pattern="one-column">
				<PageHeading icon={faTags}>{pageTitle}</PageHeading>
				<section className="mt-8">
					<TaxonomyList>
						{allTagsGroupData.map(tagsGroupData => {
							const findTagData = allTagContentData.find(
								tagContentData =>
									tagContentData.node.fields.name === tagsGroupData.fieldValue
							)
							return (
								<TaxonomyListItem
									key={findTagData.node.id}
									item={findTagData}
									total={tagsGroupData.totalCount}
									icon={faTag}
								/>
							)
						})}
					</TaxonomyList>
				</section>
			</Layout>
		</AppContext.Provider>
	)
}

export default TagsListTemplate
